/* Reset */
html,body {width:100%; height:100%; background-color: #f2f4f9;}
body * {box-sizing: border-box; outline: none;}
body.fix{overflow-y: hidden;}
html,body,h1,h2,h3,h4,h5,h6,div,p,blockquote,pre,code,address,ul,ol,li,menu,nav,section,article,aside,dl,dt,dd,table,thead,tbody,tfoot,label,caption,th,td,form,fieldset,legend,hr,input,button,textarea,object,figure,figcaption {margin:0;padding:0;}
body{-webkit-text-size-adjust:none;word-wrap:break-word;word-break:break-all;}
body,input,select,textarea,button {border:none; font-size:17px; font-family: 'AppleSDGothicNeo', sans-serif; color:#333333;}
ul,ol,li{list-style:none;}
table{border-spacing:0;}
img,fieldset{border:0;}
address,cite,code{font-style:normal;font-weight:normal;}
em,i {font-style:normal;}
label,img,input,select,textarea,button{vertical-align:middle;}
.hide,legend{overflow: hidden; display:block; position:absolute; border: 0; width: 1px; height: 1px; clip: rect(1px, 1px, 1px, 1px);}
hr{display:none;}
main,header,section,nav,footer,aside,article,figure{display:block;}
a{color:#333333;text-decoration:none;}
button {cursor: pointer; color: #333333; background-color: transparent; outline: none;}
*{-webkit-tap-highlight-color: transparent;}

/* Form */
.re_none {resize: none;}
input[type=password],
input[type=tel],
input[type=time],
input[type=text],
input[type=search],
input[type=email],
input[type=file],
input[type=url],
input[type=number],
input[type=date],textarea {font-size:17px; color:#333333; background-color:#fff; border: 1px solid #dddddd; box-sizing: border-box; vertical-align:middle; border-radius: 6px; -webkit-appearance: none; -moz-appearance: none; appearance: none;}
pre { white-space: -moz-pre-wrap; white-space: -pre-wrap; white-space: -o-pre-wrap; white-space: pre-wrap; word-wrap: break-word; }
input[type=password]::-moz-reveal,
input[type=password]::-ms-reveal {display: none; width: 0; height: 0;}
input[type=email],
input[type=number],
input[type=password],
input[type=text] {padding: 12px; height: 40px; line-height: 40px; border-radius: 6px;}
input::-webkit-input-placeholder{ color:#999999; font-size:16px;}
textarea {padding: 12px;}
textarea::-webkit-input-placeholder {color:#999999;}
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {-webkit-appearance: none; margin: 0;}
input[type=number] {-moz-appearance:textfield;}
input[type=text]::-ms-clear {display: none;}
select{-webkit-appearance:none; -moz-appearance:none; appearance:none;}
select::-ms-expand{display:none;}

textarea[readonly]{padding:12px; font-size:16px; color:#868a95; font-weight:normal; line-height:140%; height:78px; background-color:#fafafa;border:1px solid #e1e1e1;}
textarea[disabled]{padding:12px; font-size:16px; color:#868a95; font-weight:normal; line-height:140%; height:78px; background-color:#e4e6ef;border:1px solid #e1e1e1;}

textarea:focus,input:focus{border:1px solid #258afe;outline:none;}
input[disabled],
input[readonly]{background-color: #f8f9fb; color: #969aa6;}
/* input[disabled]::-webkit-input-placeholder,
input[readonly]::-webkit-input-placeholder{ color:#969aa6; font-size:16px;} */
.clear {clear:both;}
.clear:after { content:""; display:block; clear:both;}
.left{float: left;}
.right{float: right;}

/* input */
.custom_date{display: inline-block; position: relative; background-color: #fff; border: 1px solid #dddddd; border-radius: 5px;}
input.date{padding: 0 38px 0 10px; height: 40px; line-height: 41px; background-color: #fff; border: none !important; background: url('../images/icon/input_date.png') no-repeat right 9px center !important; cursor: pointer;}
.custom_date::after{content: ""; width: 1px; height: 38px; background-color: #dddddd; position: absolute; right: 38px; top: 50%; transform: translateY(-50%);}
input.date:focus{border:1px solid #258afe !important; outline:none;}
/* input[disabled].date{background-image: url('../images/icon/input_date_disabled.png');} */

/* select */
.select {padding:0 16px 0 12px; height: 40px; line-height: 40px; background: #fff url('../images/icon/select_down.png') no-repeat right 16px center; border-radius: 6px; border: 1px solid #dddddd; font-size: 17px; cursor: pointer;}
.select:focus {background-image: url('../images/icon/select_up.png'); border:1px solid #258afe;}

/* 체크박스, 라디오*/
.label {display: inline-block; padding-left: 27px; height: 18px; line-height: 18px; font-weight: 400; cursor: pointer;}
.label.single{padding-left: 18px;}

.ch_check + .label {background: url('../images/icon/checkbox_off.png') no-repeat left top;}
.ch_check:checked + .label { background-image: url('../images/icon/checkbox_on.png');}
/* .ch_check:checked[disabled] + .label { background-image: url('../images/icon/checkbox_on.png'); color: #222222;} */

.ch_radio + .date_label {display: inline-block; width: 70px; height: 40px; line-height: 40px; color: #258afe; background-color: #dfeeff; border-radius: 5px; text-align: center; cursor: pointer;}
.ch_radio:checked + .date_label {background-color: #fff; border: 1px solid #258afe;}
/* .ch_radio:checked[disabled] + .label { background-image: url('../images/icon/radio_disabled.png'); color: #222222;} */

/* scroll */
.scroll {overflow-y: auto !important; overflow-x: hidden;}
.scroll::-webkit-scrollbar {width: 4px; height: 4px;}
.scroll::-webkit-scrollbar-thumb {background-color:#999999; border-radius: 100px;}
.scroll::-webkit-scrollbar-track {background-color:#999999; border-radius: 100px; background-clip: padding-box; border: 3px solid transparent;}

/* display */
.dis_block {display: block;}
.dis_inblock {display: inline-block;}
.dis_none {display: none;}

/* margin */
.marleft_1 {margin-left: 1px;}
.marleft_5 {margin-left: 5px;}
.marleft_10 {margin-left: 10px;}
.marleft_14 {margin-left: 14px;}
.marleft_16 {margin-left: 16px;}
.marleft_60 {margin-left: 60px;}
.martop_4 {margin-top: 4px;}
.martop_10 {margin-top: 10px;}
.martop_15 {margin-top: 15px;}
.martop_17 {margin-top: 17px;}
.martop_20 {margin-top: 20px;}
.martop_30 {margin-top: 30px;}
.martop_33 {margin-top: 33px;}
.marbtm_4 {margin-bottom: 4px;}

/* padding */
.pdtop_8{padding-top: 8px;}
.pdbtm_4{padding-bottom: 4px;}
.pd_none{padding:0 !important;}

/* width */
.w_70{width: 70px;}
.w_100{width: 100px;}
.w_120{width: 120px;}
.w_130{width: 130px;}
.w_140{width: 140px;}
.w_180{width: 180px;}
