/* height */
.h_631{height: 631px;}
.h_full{height: 100%;}

/* color */
.col_blue{color: #008dff;}
.col_black{color: #222222;}
.col_grey{color: #666666;}
.col_grey2{color: #999999;}
.col_red{color: #f45f5f !important;}

/* font-size */
.fs_10 {font-size: 10px;}
.fs_16 {font-size: 16px;}
.fs_19 {font-size: 19px;}
.fs_43 {font-size: 43px;}

/* text-align */
.txt_center {text-align: center;}
.txt_left {text-align: left;}
.txt_right {text-align: right;}

/* ellipsis */
.ellipsis{display:inline-block; overflow:hidden; white-space:nowrap; text-overflow:ellipsis; vertical-align: middle;}

/* font-weight */
.f_light {font-weight: 300;}
.f_normal {font-weight: 400 !important;}
.f_bold {font-weight: 700;}
.f_ebold {font-weight: 800;}

/* btn, icon */
.btn_center {text-align: center;}
.btn_center * ~ * {margin-left: 10px;}
.blue_btn{display: inline-block; height:40px; line-height:41px; border-radius:5px; background-color:#008dff; color:#fff; font-size:16px;}
.blue_btn.disabled{background-color:#999999;}
.blue_btn:not(.disabled):hover{background-color:#008cffc2; transition: all .5s;}
.black_btn{display: inline-block; height:40px; line-height:41px; border-radius:5px; background-color:#222222; color:#fff; font-size:16px;}
.black_btn:hover{background-color:#222222d2; transition: all .5s;}
.white_btn{display: inline-block; height:40px; line-height:41px; border-radius:5px; background-color:#fff; border: 1px solid #bbbbbb; color:#222222; font-size:16px;}
.white_btn:hover{background-color:#f9f9f9; transition: all .5s;}

/* header, gnb */
header{width:260px; height:100vh; position:fixed; left:0; top:0; z-index:10; background-color:#fff;}
header h1{height:90px; border-right:1px solid #f2f2f2;}
header h1 a{display:block; padding-top: 19px; width:100%; height:100%; text-align: center;}
header h1 a img{height: 48px;}

.gnb{padding-top: 40px; background-color: #2a3042; height: 100%;}
.gnb ul{border-top: 1px solid #404555;}
.gnb ul li{border-bottom: 1px solid #404555;}
.gnb a{display: block; padding: 26px 31px 26px 29px; color:#fff; background: url('../images/icon/gnb_aorrw_off.png') no-repeat right 30px center; font-size: 19px;}
.gnb li.active a{background-color: #008dff; background-image: url('../images/icon/gnb_aorrw_on.png');}
.gnb li:hover a{background-color: #008cff6e; background-image: url('../images/icon/gnb_aorrw_on.png'); transition: all .5s;}
.gnb li.active:hover a{background-color: #008dff;}

/* footer */
footer{height: 60px;padding: 20px 0; width:calc(100% - 260px); margin-left:260px; background-color: #f2f4f9; text-align: center;}
footer p{color: #666666; font-size: 16px;}

/* main */
main{width:calc(100% - 260px); min-height:calc(100vh - 60px); margin-left:260px; background-color:#f2f4f9; position:relative;}
.my_nav{height: 90px; position:relative; background-color:#fff; box-shadow: 2px 2px 5px rgba(0, 0, 1, 0.08);}
.my_nav ul{height: 36px; padding-right:40px; position:absolute; top:50%; right:0; transform:translateY(-50%);}
.my_nav ul li{float:left; margin-right:10px;}
.my_nav ul li.user_name{position: relative; margin-right:20px; margin-top: 3px; padding: 6px 0 6px 41px; color: #202020;}
.my_nav ul li.user_name::before{content: "";  position: absolute; left: 0; top: 50%; transform: translateY(-50%); width: 31px; height: 31px; border-radius: 50%; background: #f2f4f9 url('../images/icon/user_name.png') no-repeat center;}

.my_nav ul li button{display:block; width: 116px; height:36px; line-height:36px; background:no-repeat left 22px center;border: 1px solid #e4e4e4; border-radius: 18px; box-shadow: 2px 2px 10px rgba(0, 0, 1, 0.05); color:#24292e; font-size: 16px; text-align: center;}
.my_nav ul li button.my_menu{padding-left: 20px; background-image:url('../images/icon/my_menu.png'); color: #258afe;}
.my_nav ul li button.logout{padding-left: 20px; background-image:url('../images/icon/logout.png');}
main .con_wrap{padding:40px 40px 0;}

/* modal Dim */
#mainDimLayer{display:block; width:100%; height:100%; background-color: rgba(0, 0, 0, 0.2); position:fixed; left:0; top:0px; margin:0; padding:0;z-index:101;}
.subDimLayer{display: block;width: 100%;height:100%; background-color: rgba(0, 0, 0, 0.2); position: fixed;left: 0;top: 0px;margin: 0;padding: 0;z-index: 103;}

/* modal */
.modal_wrap{min-width:400px; max-height:700px; background: #ffffff;box-sizing: border-box; display: none; border-radius: 8px; position: fixed; top: 50%;left: 50%;
-webkit-transform: translate(-50%, -50%);-ms-transform: translate(-50%, -50%);-moz-transform: translate(-50%, -50%);-o-transform: translate(-50%, -50%);transform: translate(-50%, -50%);display: none;z-index: 102;
border-radius:10px; overflow:hidden;}
.modal_wrap.active{display: block;}

.modal_wrap .modal_section{padding-bottom:30px;}
.modal_wrap .modal_section .modal_header{padding:24px 30px 23px; border-bottom: 1px solid #e4e4e4;}
.modal_wrap .modal_section .modal_header .modal_title{font-size: 26px; font-weight: 800; color:#202020;}
.modal_wrap .modal_section .modal_header .hide_btn{position:absolute; right:29px; top:30px; width:21px; height:21px; background:url('../images/icon/modal_close.png') no-repeat center;}
.modal_wrap .modal_section .modal_contents{padding:30px;}
.modal_wrap .modal_section .btn_wrap{text-align: center; position:relative;}
.modal_wrap .modal_section .btn_wrap button ~ button{margin-left:8px}

.modal_wrap.alert,
.modal_wrap.confirm{width:410px; z-index:104;}
.modal_wrap.alert .modal_section,
.modal_wrap.confirm .modal_section{padding-bottom:30px;}
.modal_wrap.alert .hide_btn,
.modal_wrap.confirm .hide_btn{width:21px; height:21px; background:url('../images/icon/modal_close.png') no-repeat center/contain; position:absolute; right:29px; top:26px;}
.modal_wrap.alert .modal_text,
.modal_wrap.confirm .modal_text{width: 100%; text-align:center; padding-top:103px; padding-bottom: 53px; font-size:26px;}
.modal_wrap.alert .btn_wrap,
.modal_wrap.confirm .btn_wrap{padding-top:20px;}

/* 전체 */
#wrap{min-width:1336px;}
.title_box{padding: 0 4px 19px;}
.title{font-size: 26px; font-weight: 800;}

.title_box > ul{margin-top: 15px;}
.title_box > ul li{position: relative; float: left; font-size: 15px; color: #666666;}
.title_box > ul li:first-child{padding-left: 25px;}
.title_box > ul li:first-child::before{content: ""; width: 15px; height: 12px; position: absolute; left: 0; top: 45%; transform: translateY(-50%); background: url('../images/icon/home.png') no-repeat left center;}
.title_box > ul li ~ li{margin-left: 15px; padding-left: 18px;}
.title_box > ul li ~ li::before{content: ">"; position: absolute; left: 0; opacity: 0.5;}

.tilde{display: inline-block; width: 18px; text-align: center;}
.table_wrap{position: relative; padding-bottom: 20px; border-radius: 10px; overflow: hidden; background-color: #fff; border: 1px solid #e4e4e4; box-shadow: 2px 2px 10px rgba(0, 0, 1, 0.05);}
.inp_wrap{position: relative; padding: 19px 30px 21px 32px; border-radius: 10px; background-color: #fff; border: 1px solid #e4e4e4; box-shadow: 2px 2px 10px rgba(0, 0, 1, 0.05);}
.btn_bottom{position: absolute; bottom: 20px; right: 40px;}

.date_radio{display: inline-block; vertical-align: middle;}
.date_radio li{float: left;}
.date_radio li ~ li{margin-left: 5px;}

.break_section{display: inline-block;}

@media screen and (max-width:1970px) {
    .break_section{display: block; margin-top: 20px; margin-left: 0 !important;}
}

/* table */
table.txt_left4 tbody tr td:nth-child(4) {text-align: left;}

table.txt_left1 tbody tr th:nth-child(1),
table.txt_left1 tbody tr td:nth-child(1) {text-align: left;}

table{width:100%;}
table thead th,
table tbody th,
table tbody td,
table tbody th{text-align:center; padding:24px 0; height:60px; font-size: 17px;}
table th{font-weight:700; color:#333;}
table td{color:#666666;}
table .required:after{content:'*'; color:#e82828; margin-left:4px;}
table.layout_fixed{table-layout:fixed;}

