.table_wrap table.type2 tbody td{padding: 0 15px; height: 70px; border-bottom: 1px solid #e4e4e4;}

table.type1.tdpd_5 tbody td { padding: 0 5px; }
table.left2_table tbody th,
table.left2_table tbody td { text-align: left; }
table.left2_table th { width: 259px; }
@media screen and (max-width:1500px) {
