}

/* pagination */
.page_wrap{position: relative; min-height:40px;}
.pagination{display: inline-block; width: 60%; position: absolute; left: 50%; transform: translateX(-50%);}
.pagination ul{display: flex;justify-content: center; align-items: center;}
.pagination ul li ~ li{margin-left: 10px;}
.pagination ul li button{min-width: 40px; height: 40px; line-height: 40px; color: #333; font-size: 16px; border: 1px solid #dddddd; border-radius: 5px; font-weight: 700; text-align: center;}
.pagination ul li button.paging_left{background: url('../images/icon/page_left.png') no-repeat center;}
.pagination ul li button.paging_right{background: url('../images/icon/page_right.png') no-repeat center;}
.pagination ul li button.paging_first{background: url('../images/icon/page_left_first.png') no-repeat center;}
.pagination ul li button.paging_last{background: url('../images/icon/page_right_end.png') no-repeat center;}
.pagination ul li .paging_num{width: auto; padding: 0 6px; line-height: 17px;}
.pagination ul li .paging_num.on{border: 1px solid #dddddd; background-color: #008dff; border-radius: 5px; color: #fff;}


